import localforage from 'localforage';
export var getOrCreateTable = function (storeName) {
    return localforage.createInstance({
        name: 'Uspacy',
        storeName: storeName,
    });
};
var table = getOrCreateTable('tokens');
export var setToken = function (token) {
    return table.setItem('token', token);
};
export var getToken = function () {
    return table.getItem('token');
};
export var removeToken = function () {
    return table.removeItem('token');
};
